















import Vue from "vue";
import Component from "vue-class-component";
import { MixinKeyShortcuts } from "../index";

@Component({
  mixins: [MixinKeyShortcuts],
  shortcuts: {
    default: {
      keys: ["a", "s", "d", "f"],
      handle() {
        alert("You have pressed A-S-D-F. So are you boring now?");
      }
    },
    foo: [
      {
        key: "g",
        modifiers: { meta: true },
        handle() {
          alert("trigger: CMD + G");
        }
      }
    ],
    bar: [
      {
        key: "k",
        modifiers: { meta: true },
        handle() {
          alert("trigger: CMD + K");
        }
      }
    ]
  }
})
export default class ExampleKeyShortcuts extends Vue {}
