























import Vue from "vue";
import Component from "vue-class-component";

@Component({
  inject: ["announce", "setBusy", "clear"],
  watch: {
    busy(value) {
      this.setBusy(value);
    }
  }
})
export default class VueLiveExample extends Vue {
  value: string = "";
  busy: boolean = false;
  force: boolean = false;
}
