













import Vue from "vue";
import Component from "vue-class-component";
import { MixinId } from "../index";

const ExampleIdInputInterface = Vue.extend({
  props: {
    label: String,
    type: String,
    value: String
  }
});

@Component({
  mixins: [MixinId],
  created() {
    this.localValue = this.value;
  },
  watch: {
    value(v) {
      this.localValue = v;
    },
    localValue(v) {
      if (this.value !== v) {
        this.$emit("input", v);
      }
    }
  }
})
export default class ExampleIdInput extends ExampleIdInputInterface {
  localValue: string = "";
}
