



























import Vue from "vue";
import Component from "vue-class-component";
import { MixinKeyTravel } from "../index";

interface ExampleOption {
  text: string;
  value: string;
}

@Component({
  mixins: [MixinKeyTravel]
})
export default class ExampleKeyTravel extends Vue {
  autofocus = true;
  orientation = "vertical";

  options: Array<ExampleOption> = [
    { text: "Foo", value: "x" },
    { text: "Bar", value: "y" },
    { text: "Baz", value: "z" }
  ];
  value: string = "y";

  getAutofocusItem() {
    return this.$refs.btn;
  }
  getKeyItems() {
    return this.$refs.items;
  }
  goCurrentItem() {
    const items = <Array<HTMLElement>>this.getKeyItems();
    const index = this.options.map(option => option.value).indexOf(this.value);
    const currentItem = items[index];
    if (currentItem) {
      currentItem.focus();
    }
  }
  fireAction(item: any) {
    const items = <Array<HTMLElement>>this.getKeyItems();
    const index = items.indexOf(item);
    if (index >= 0) {
      this.value = this.options[index].value;
    }
  }
}
